import React, { useContext, useState } from "react";
import C from './Home.module.css'
import UserContext from '../../userContext'
import { Link, Redirect } from "react-router-dom";
import img from '../Users/img_def.jpg'
import SpinerHOC from "../../HOC/SpinerHOC";
import { connect } from "react-redux";
// import { get_status } from "../../redux/User/User.redux";
import { get_status } from "../../redux/Home/Home.redux";

const Home = (props) => {
    // const context = useContext(UserContext)
    const [is_input,setInput]=useState(false)
        // console.log(props);
    
    const ResultAllContacts = () => {
        const cont = props.data.contacts
        if (!cont) return []

        const arrContacts = []
        for (const key in cont) {
            if (cont[key]) {
                arrContacts.push({
                    name: key,
                    src: cont[key]
                })
            }
        }
        return arrContacts
    }
    // const openA = (e) => {
    //     e.preventDefault()
    // }

    const about_me_click=()=>{
        setInput(true);
    }
    const about_me_blur=(el)=>{
        console.log(el.target.value);
        props.api.profile.status_put(el.target.value)
        props.get_status(props.api,props.id)
        setInput(false)
    }

    console.log(props);
    return <div>
        <div className={C.main_inf}>
            <img src={props.data.photos?.large ? props.data.photos.large : img} />
            <div>
                <h3>{props.data.fullName}</h3>
                {is_input?
                <input type='text' autoFocus={true} onBlur={about_me_blur}/>
                :
                <p onDoubleClick={about_me_click}>
                    {props.home.aboutMe?props.home.aboutMe: props.text?.no_information}
                </p>
                }
            </div>
            <div>
                <h4>
                    {props.text?.contacts}
                </h4>
                <div className={C.contacts_a}>
                { ResultAllContacts().map(d=><a key={d.name+'_id'} href={'https://'+d.src}>{d.name}</a>)}
                </div>
                
               
            </div>
            <div>
                <Link to={`/Mesager/${props.data.fullName}/${props.id}`} >
                    {props.text?.start_chat}
                </Link>
            </div>
        </div>
    </div>
}

const mapStateToProps=state=>{
    return {
        api:state.api,
        home:state.home
    }
}

export default SpinerHOC(connect(mapStateToProps,{get_status})(Home))
